import React from "react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { YearSelector } from "./../components/YearSelector";
import "./MatchPage.scss";

import { MatchDetailCard } from "../components/MatchDetailCard";
export const MatchPage = () => {
  const [match, setMatches] = useState([]);
  const { teamName, year } = useParams();
  const years = [];

  useEffect(() => {
    const fetchMatches = async () => {
      const response = await fetch(
        `http://localhost:8080/teams/${teamName}/matches?year=${year}`
      );
      const data = await response.json();
      console.log(data);
      setMatches(data);
    };
    fetchMatches();

    console.log("hello world");
  }, [teamName, year]);

  {
    for (var i = 2008; i <= 2020; i++) {
      years.push(i);
    }
  }
  return (
    <div className="MatchPage">
      <div className="yearSelector">
        <h3 className="page-heading"> Select Year</h3>
        <ol>
          {years.map((e) => (
            <li>
              <YearSelector key={e} teamName={teamName} year={e} />
            </li>
          ))}
        </ol>
      </div>
      <div>
        <h1>{teamName}</h1>
        {match.map((e) => (
          <MatchDetailCard key={e.id} teamName={teamName} matches={e} />
        ))}
      </div>
    </div>
  );
};
