import React from "react";
import { Link } from "react-router-dom";
import "./MatchSmallCard.scss";

export const MatchSmallCard = ({ teamName, matches }) => {
  const name = teamName === matches.team1 ? matches.team2 : matches.team1;
  const route = `/teams/${name}`;
  const isMatchWon = teamName === matches.winner;
  return (
    <div
      className={
        isMatchWon ? "MatchSmallCard win-card" : "MatchSmallCard loss-card"
      }
    >
      <p>vs </p>
      <h4>
        <Link to={route}> {name}</Link>
      </h4>
      <p>
        {matches.winner} won by {matches.resultMargin} {matches.result}
      </p>
    </div>
  );
};
