import React from "react";
import { Link } from "react-router-dom";
import "./MatchDetailCard.scss";

export const MatchDetailCard = ({ teamName, matches }) => {
  if (!matches) {
    return null;
  }
  const name = teamName === matches.team1 ? matches.team2 : matches.team1;
  const isMatchWon = teamName === matches.winner;
  const route = `/teams/${name}`;
  return (
    <div
      className={
        isMatchWon ? "win-card MatchDetailCard" : "loss-card MatchDetailCard"
      }
    >
      <div>
        <span className="vs">vs</span>
        <h3>
          <Link to={route}> {name}</Link>
        </h3>
        <h3 className="match-venue">At {matches.venue}</h3>
        <h3 className="match-date">on {matches.date}</h3>
        <h3 className="match-result">
          {matches.winner} won by {matches.resultMargin} {matches.result}
        </h3>
      </div>
      <div className="additional-details">
        <h3>First Inings</h3>
        <p>{matches.team1}</p>
        <h3>Second Inings</h3>
        <p>{matches.team2}</p>
        <h3>Man of the Match</h3>
        <p>{matches.playerOfMatch}</p>
        <h3>Umpire</h3>
        <p>
          {matches.umpire1},{matches.umpire2}
        </p>
      </div>
    </div>
  );
};
